import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClientService } from 'src/app/service/http-client.service';
import { Sarees } from '../../sarees';

@Component({
  selector: 'app-viewsarees',
  templateUrl: './viewsarees.component.html',
  styleUrls: ['./viewsarees.component.css']
})
export class ViewsareesComponent implements OnInit {
  @Input()
  saree:Sarees;
  @Output()
  sareeDeletedEvent = new EventEmitter();

  constructor(private httpClientService:HttpClientService,
    private router:Router) { }

  ngOnInit() {
  }
  deleteSaree(){
    this.httpClientService.deleteSaree(this.saree.id).subscribe(
      (saree)=>{
        this.sareeDeletedEvent.emit();
        this.router.navigate(['admin','sarees']);
      }
    );
  }
  editSaree(){
    this.router.navigate(['admin','sarees'],{queryParams:{action:'edit',id:this.saree.id}});
  }

}
