import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { User } from 'src/app/User';
import { Sarees } from '../admin/sarees';

@Injectable({
  providedIn: 'root'
})
export class HttpClientService {

  constructor(private httpClient : HttpClient) { }
  getUsers(){
    return this.httpClient.get<User[]>(`http://localhost:8080/users/get`);
  }
  addUser(newUser : User){
   return this.httpClient.post<User>(`http://localhost:8080/users/add`,newUser);
  }
  deleteUser(id){
   return this.httpClient.delete<User>(`http://localhost:8080/users/`+ id);
  }
  getSarees(){
    return this.httpClient.get<Sarees[]>(`http://localhost:8080/sarees/get`);
  }
  addSaree(newSaree:Sarees){
   return this.httpClient.post<Sarees>(`http://localhost:8080/sarees/add`,newSaree);
  }
  deleteSaree(id){
    return this.httpClient.delete<Sarees>(`http://localhost:8080/sarees/`+id);
  }
  updateSaree(updateSaree : Sarees){
   return  this.httpClient.put<Sarees>(`http://localhost:8080/sarees//update`,updateSaree);
  }
}
