import { HttpClient } from '@angular/common/http';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClientService } from 'src/app/service/http-client.service';
import { Sarees } from '../../sarees';

@Component({
  selector: 'app-addsaree',
  templateUrl: './addsaree.component.html',
  styleUrls: ['./addsaree.component.css']
})
export class AddsareeComponent implements OnInit {
  @Input()
  saree :Sarees;
  @Output()
  sareeAddedEvent = new EventEmitter();
  private selectedFile;
  imgURL:any;

  constructor(private httpClientService:HttpClientService,
    private httpClient: HttpClient,
    private route : Router,
    private activeRouter: ActivatedRoute
    ) { }

  ngOnInit() {
  }
  public onFileChanged(event){
    console.log(event);
   this.selectedFile = event.target.files[0]; 

   let reader = new FileReader;
   reader.readAsDataURL(event.target.files[0]);
   reader.onload=(event2)=>{
     this.imgURL=reader.result;
   };
  }
  saveSaree(){
    if(this.saree.id===null){
    const uploadData = new FormData();
    uploadData.append('imageFile',this.selectedFile,this.selectedFile.name);
    this.selectedFile.imageName=this.selectedFile.name;

    this.httpClient.post(`http://localhost:8080/sarees/upload`,uploadData,
    {observe: 'response'}).subscribe((response)=>{
      if (response.status===200) {
        this.httpClientService.addSaree(this.saree).subscribe(
          (saree)=>{
            this.sareeAddedEvent.emit();
            this.route.navigate(['admin','sarees']);
          }
        );
        console.log('upload sucessfull');
      }else{
        console.log('Upload failed');
      }
    });
  }else{
    this.httpClientService.updateSaree(this.saree).subscribe(
      (saree)=>{
        this.sareeAddedEvent.emit();
        this.route.navigate(['admin','sarees']);
      }
    );
  }
}
}
